*{
  font-family: Arial, Helvetica, sans-serif;
}
body{ 
  padding: 60px; 
}
input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}
.Card-Grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.Card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.Card p {
padding: 5px;
text-align: center;

}
.Card img {
  max-height: 170px;
}
